<template lang="pug"> 
    tr
        td {{skill.name}}
        td 
            input(
                type="text"
                :value="skill.percents"
            ).percents
        td %
        td 
            button(
                @click="removeSkill"
                type="button"
                ) Удалить         
</template>
    
<script>
export default {
    props: {
        skill: Object
    },
    methods: {
        removeSkill() {
            this.$emit('removeSkill', this.skill.id);
        }
    }
}
</script>

<style src="styles/skillsList.scss" lang="scss" scoped>

</style>


