<template lang="pug">  
.blog
    .page-title Страница &laquo;Блог&raquo;
    form.form(action="/api/blog" method="POST" enctype="multipart/form-data")
        .form-title Добавить запись
        .field
            input(
                name="title"
                placeholder="Название"
                v-model="newArticle.title"
            ) 
        .field
            input(
                name="date"
                placeholder="Дата"
                v-model="newArticle.date"
            )
        .field
            textarea(
                name="body"
                rows="10" placeholder="Содержание"
                v-model="newArticle.body"
            )
        .saveButton 
            button(
                type="button"
                @click="addToApi"
            ) Добавить
</template>

<script>
import {mapMutations} from 'vuex';
import {Validator} from 'simple-vue-validator';
import axios from 'axios';

export default {
    mixins: [require('simple-vue-validator').mixin],
    validators: {
        title(value) {
            return Validator.value(value).required('Поле не может быть пустым')
        },
        date(value) {
            return Validator.value(value).required('Поле не может быть пустым')
        },
        body(value) {
            return Validator.value(value).required('Поле не может быть пустым')
        }
    },
    props: {
        articles: Object
    },
   data() {
       return {
           newArticle: {title:'', date:'', body:''},
       }
   },
   methods: {       
       addToApi() {
           axios.post('./api/blog', this.newArticle)
           .then((response) => {
               console.log(response);
           })
           .catch((error) => {
               console.log(error);
           })
       }
    }
}
</script>

<style src="styles/form.scss" lang='scss' scoped>

</style>


